/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464d53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}

.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.btn {
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  cursor: pointer;
}

.cardStyle {
  margin-bottom: 20px;
  border: 1px solid #6c757d;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.titleStyle {
  font-size: 24px;
  color: var(--orange);
}

.textStyle {
  font-size: 16px;
  color: white;
}

.background-images-create-class {
  position: relative;
  width: 100%;
  height: 100vh;
  color: white;
  background-image: url("/src/assets/scheduleBg.jpg");
  background-size: cover;
  background-position: center;
  opacity: 0.5;
}

.blur {
  background: rgba(253, 128, 43, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}

.swal-custom-style {
  background-color: rgba(0, 0, 0, 0.5);
  color: #ff8400;
}

.datos-temp {
  display: flex;
  color: white;
  padding: 0.1rem;
}

.error-message {
  color: rgb(250, 1, 1);
  font-size: 15px;
  margin-top: 5px;
  font-weight: bold;
}
